import axios from 'axios';

//=============================================================================+
//  
//=============================================================================+
export function addCategory(params) {
    return axios({
        method:"post"
        ,url: ('/api/categories')
        ,data: params
    });
}
//=============================================================================+
//   
//=============================================================================+
export function getCategoryList(params) {
    return axios({
        method:"get"
        ,url: ('/api/categories')
        ,data: params
    });
}
//=============================================================================+
//   
//=============================================================================+
export function setCategoryOrder(category) {
    return axios({
        method:"put"
        ,url: ('/api/categories/update/order')
        ,data: category
    });
}

//=============================================================================+
//    
//=============================================================================+
export function getCategory(id, params) {
    return axios({
        method:"get"
        ,url: ('/api/categories/' + id)
        ,data: params
    });
}
//=============================================================================+
//   
//=============================================================================+
export function updateCategory(id, params) {
    return axios({
        method:"put"
        ,url: ('/api/categories/' + id)
        ,data: params
    });
}
//=============================================================================+
//    
//=============================================================================+
export function updateShowStatusCategory(id, params) {
    return axios({
        method:"put"
        ,url: ('/api/categories/' + id + '/status')
        ,data: params
    });
}
//=============================================================================+
//   
//=============================================================================+
export function deleteCategory(id, params) {
    return axios({
        method:"delete"
        ,url: ('/api/categories/' + id)
        ,data: params
    });
}