<template>
    <div class="contents_wrapper">
        <h2 class="align-center"></h2>
        <div class="h_center sign_up_wrapper">
            <input type="text" class="input_nor width_100" v-model="user.user_id" autocomplete="off" placeholder=""/>
            <input type="password" class="input_nor width_100" v-model="user.password" autocomplete="off" placeholder=""/>
            <input type="password" class="input_nor width_100" v-model="user.check_password" autocomplete="off" placeholder=" "/>
            <input type="text" class="input_nor width_100" v-model="user.user_name" autocomplete="off" placeholder=""/>
            <input type="text" class="input_nor width_75" style="margin-right: 12px;" v-model="user.postcode" autocomplete="off" placeholder="" readonly/>
            <button style="height:30px; border-radius: 2px; float: right" class="width_20 btn_nor" @click="showPostPopup"></button>
            <input type="text" class="input_nor width_100" v-model="user.address" autocomplete="off" placeholder="" readonly/>
            <input type="text" class="input_nor width_100" v-model="user.address_detail" autocomplete="off" placeholder="" ref="address_detail"/>
            <input type="tel" class="input_nor width_100" v-model="user.phone" autocomplete="off" placeholder=" " />
            <input type="text" class="input_nor width_100" v-model="user.email" autocomplete="off" placeholder="" />
            <div class="font_11 margin_top_12 margin_bottom_20">
                <div class="checkbox_white">
                    <input type="checkbox" id="check01">
                    <label for="check01"><span class="col_red">()</span>        </label>
                </div>
                <div class="margin_top_5 checkbox_white">
                    <input type="checkbox" id="check02">
                    <label for="check02">()    </label>
                </div>
            </div>
            <button class="btn_nor margin_auto width_100" value=" "  style="border-radius: 4px" @click="signUp"> </button>
        </div>
        <postcode-popup v-show="UI.IS_SHOW_POST_POPUP"
                        @hidePostPopup="hidePostPopup"
                        @handleAddress="handleAddress">
        </postcode-popup>
    </div>
</template>

<script>
    import * as SIGNUP_API from '../api/login';
    import PostcodePopup from "../components_user/PostcodePopup";
    export default {
        name: "SignUp",
        components: {PostcodePopup},
        data:function(){
            return{
                user: {
                    user_id: '',
                    password: '',
                    check_password: '',
                    user_name: '',
                    postcode: '',
                    address: '',
                    address_detail: '',
                    phone: '',
                    email: ''
                },
                UI: {
                    IS_SHOW_MENU: false,
                    IS_SHOW_POST_POPUP: false,
                }
            }
        },
        methods:{
            closeMenu:function(){
                this.$parent.$data.UI.IS_SHOW_SIGN_UP = false;
            },

            isDuplicatedId: async function(){
                try {
                    const user_id = {user_id: this.user.user_id };
                    const rs = await SIGNUP_API.idDuplicationChk(user_id);
                    if (rs.data.code === 'fail') {
                        alert('   ');
                        return true;
                    } else if (rs.data.code === 'success'){
                        alert('  .');
                        return true;
                    }
                } catch (err) {
                    console.error(err);
                }
            },

            async signUp() {
                //   
                if(this.user.user_id === ''){
                    alert(' ');
                    return;
                }
                // // 
                // if(this.isDuplicatedId()){
                //     return;
                // }
                //   
                if(this.user.password === ''){
                    alert(' ');
                    return;
                }
                //    
                if(this.user.check_password === ''){
                    alert('   ');
                    return;
                }

                //  
                if(this.user.password !== this.user.password){
                    alert('  .');
                    return;
                }

                //   
                if(this.user.user_name === ''){
                    alert(' .');
                    return;
                }

                //   
                if(this.user.postcode === '' || this.user.address === '' || this.user.address_detail === ''){
                    alert(' .');
                    return;
                }

                //    
                if(this.user.phone === ''){
                    alert('  .');
                    return;
                }

                //   
                if(this.user.email === ''){
                    alert(' .');
                    return;
                }

                const params = {
                    user: this.user,
                };

                const rs = await SIGNUP_API.signUp(params);

                if(rs.data.code === 'success'){
                    alert(' ');
                    location.href='/login';
                } else{
                    alert(' ');
                }
            },

            //--------------------------------------------------
            // 
            //--------------------------------------------------
            showPostPopup() {
                this.UI.IS_SHOW_POST_POPUP = true;
            },
            hidePostPopup() {
                this.UI.IS_SHOW_POST_POPUP = false;
            },
            handleAddress(address, code) {
                this.user.postcode = code;
                this.user.address = address;
                this.user.address_detail= '';
                this.$refs.address_detail.focus();
                this.hidePostPopup();
            },

            // -   

        }
    }
</script>

<style scoped>
    table{
        margin-bottom: 36px;
    }
    table td {
        padding: 5px 10px;
        font-size: 13px;
    }
    table td:first-child{
        width: 35%;
        padding-top: 15px;
    }
</style>