import router from './router'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

router.beforeEach(async (to, from, next) => {
  NProgress.start()

  const sessionKey = sessionStorage.getItem('sessionKey');
  const userInfo = sessionStorage.getItem(sessionKey);

  if(userInfo != null)  { //   
    if (userInfo.includes("\"role_index\":3"))  // 
      {
        if (to.path == '/admin/login') //     
        {
          next({path: '/admin'})
          NProgress.done()
        } else
          next()
      }
     else {
      if (!to.path.startsWith("/admin")) next() //        
      else
      {
        alert(" ")
        next('/')
      }
    }
  }
  else {
    if (to.path !== '/admin/login') { //    
      if (!(to.path.startsWith("/admin") || to.path.startsWith("/ADMIN"))) next() //       (login  )
      else {
        next({path : '/admin/login' }) //      
        NProgress.done()
      }
    }
    else next()
  }
})

router.afterEach(() => {
  NProgress.done()
})
